<template>
  <div class="checkboxChoice section-filters">
    <span>
      <input
        id="all"
        v-model="store.allUniversSelected"
        class="checkbox"
        type="checkbox"
        aria-describedby="productUniverse"
        aria-controls="productUniverse"
        value="all"
        @change="changeUniverse"
      />
      <label for="all">All</label>
    </span>
    <span v-for="univers in universesList" :key="univers._id">
      <input
        :id="univers.universName"
        v-model="store.productUniverses"
        class="checkbox"
        type="checkbox"
        name="productUniverse"
        :value="univers.universName"
        @change="changeUniverse"
      />
      <label :for="univers.universName">{{ univers.universName }}</label>
    </span>
  </div>
</template>

<script>
import Store from "@/store";

export default {
  props: {
    clearSelection: {
      type: Function,
      required: true
    },
    universesList: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      store: Store.state
    };
  },
  methods: {
    changeUniverse(event) {
      Store.changeUniverse(event.target.value, this.universesList);
      this.clearSelection();
    }
  }
};
</script>

