.checkboxChoice {
  span {
    display: flex;
    flex-direction: row;
    text-align: left;
    align-items: center;
  }
  .checkbox {
    -webkit-appearance: none;
    background-color: #fafafa;
    border: 3px solid #b9b9b9;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05),
      inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
    width: 24px;
    height: 24px;
    padding: 9px;
    border-radius: 30px;
    display: inline-block;
    position: relative;
    margin-right: 5px;
    margin-top: -5px;
    vertical-align: middle;
    &:hover {
      cursor: pointer;
    }
    &:focus {
      outline: none;
    }
    &:checked {
      border: 3px solid #d36c48;
