<template>
  <div id="login">
    <img src="@/assets/header_home.png" alt class="small-header" />
    <img src="@/assets/logo_home.png" alt class="logo" />
    <div class="form">
      <div>
        <label for="password">Password</label>
        <input
          v-model="password"
          type="password"
          name="password"
          @keyup.enter="login"
        />
      </div>
      <button type="button" class="search-button" @click="login">OK</button>
    </div>
    <p v-if="logginError">Wrong login or password.</p>
  </div>
</template>

<script>
import Store from "@/store";
import axios from "axios";

export default {
  data() {
    return {
      password: "",
      store: Store.state,
      logginError: false
    };
  },
  methods: {
    login() {
      axios
        .post(
          "https://corentindechomet.fr/lba/api/cockpit/authUser?token=d9f838b6fc2f25058edbde1208b611",
          {
            user: "user",
            password: this.password
          }
        )
        .then(user => {
          Store.userLogin(user);
          this.$router.push("/");
        })
        .catch(() => {
          this.logginError = true;
        });
    }
  }
};
</script>

