<template>
  <div>
    <div class="btn-reset-ctn">
      <b-button
        class="filter-button btn-reset"
        variant="outline-*"
        @click="resetFilters"
      >
        <img :src="getIcon('resetBtn.svg')" alt />
        <p class="filter-name">Reset filters</p>
      </b-button>
    </div>
    <div>
      <b-button v-b-toggle.univers class="filter-button" variant="outline-*">
        <img src="@/assets/picto-univers.png" alt />
        <img class="filter-arrow" src="@/assets/filterArrow.png" alt />
        <p class="filter-name text-uppercase">Alloys clusters</p>
      </b-button>
      <b-collapse id="univers">
        <UniversFilter
          :clear-selection="clearSelection"
          :universes-list="universesList"
        />
      </b-collapse>
    </div>
    <div
      v-for="(filterGroup, key) in store.filtersList"
      :key="key"
      :title="key"
      active
    >
      <b-button v-b-toggle="key" class="filter-button" variant="outline-*">
        <img :src="getIcon(filterGroup.icon)" alt />
        <img class="filter-arrow" src="@/assets/filterArrow.png" alt />
        <p class="filter-name text-uppercase">{{ key }}</p>
      </b-button>
      <b-collapse :id="key">
        <div class="section-filters">
          <span
            v-if="filterGroup.type === 'checkbox-forms-sizes'"
            class="checkbox-container"
          >
            <span>
              <input
                id="allFormsAndSizes"
                v-model="store.allFormsAndSizesSelected"
                class="checkbox"
                type="checkbox"
                value="all"
                @change="changeFormsAndSizes"
              />
              <label for="allFormsAndSizes">All</label>
            </span>
            <span v-for="filter in filterGroup.datas" :key="filter.filterName">
              <input
                :id="filter.filterName"
                v-model="store.formsAndSizesSelected"
                :value="filter.technicalId"
                class="checkbox"
                type="checkbox"
                @change="changeFormsAndSizes"
              />
              <label :for="filter.filterName">{{ filter.filterName }}</label>
            </span>
          </span>
          <span v-else-if="filterGroup.type === 'slider'">
            <div v-for="filter in filterGroup.datas" :key="filter.filterName">
              <div class="filter-item slider">
                <div v-if="Array.isArray(filter.unit)">
                  <div class="switch-header">
                    <span class="filter-name">{{ filter.filterName }}</span>
                    <span class="switch">
                      {{ filter.unit[0].name }}
                      <b-form-checkbox
                        v-model="filter.selectedUnit"
                        name="check-button"
                        switch
                        class="unit-switch"
                        @change="checkboxChange(filter, filterGroup.type)"
                      ></b-form-checkbox>
                      {{ filter.unit[1].name }}
                    </span>
                  </div>
                  <VueSlider
                    v-model="filter.selectedValue"
                    :min="filter.min"
                    :max="filter.max"
                    :interval="filter.interval"
                    :tooltip="'always'"
                    @drag-end="
                      addFilter(
                        filter.selectedUnit
                          ? filter.technicalId[1]
                          : filter.technicalId[0],
                        filterGroup.type,
                        filter.selectedValue
                      )
                    "
                  ></VueSlider>
                </div>
                <div v-else>
                  <span class="filter-name">{{ filter.filterName }}</span>
                  <VueSlider
                    v-model="filter.selectedValue"
                    :min="filter.min"
                    :max="filter.max"
                    :interval="filter.interval"
                    :tooltip="'always'"
                    @drag-end="
                      addFilter(
                        filter.technicalId,
                        filterGroup.type,
                        filter.selectedValue
                      )
                    "
                  ></VueSlider>
                </div>
              </div>
            </div>
          </span>
          <span v-else-if="filterGroup.type === 'select'">
            <div v-for="filter in filterGroup.datas" :key="filter.filterName">
              <div class="row">
                <div class="col-5">{{ filter.filterName }}</div>
                <div class="col-7">
                  <b-form-checkbox-group
                    v-model="filter.value"
                    :options="[1, 2, 3, 4, 5]"
                    buttons
                    @change="
                      $nextTick(() => {
                        addFilter(
                          filter.technicalId,
                          filterGroup.type,
                          filter.value
                        );
                      })
                    "
                  ></b-form-checkbox-group>
                </div>
              </div>
            </div>
          </span>
          <span v-else-if="filterGroup.type === 'select'">
            <div v-for="filter in filterGroup.datas" :key="filter.filterName">
              {{ filter.filterName }}
              <input
                v-model="filter.value"
                @keyup.enter="
                  addFilter(filter.technicalId, filterGroup.type, filter.value)
                "
              />
            </div>
          </span>
        </div>
      </b-collapse>
    </div>
  </div>
</template>

<script>
import UniversFilter from "@/components/UniversFilter";
import VueSlider from "vue-slider-component";
import "vue-slider-component/theme/default.css";
import Store from "@/store";
import axios from "axios";

export default {
  components: {
    VueSlider,
    UniversFilter
  },
  props: {
    alloys: {
      type: Array,
      required: true
    },
    clearSelection: {
      type: Function,
      required: true
    },
    clearSorting: {
      type: Function,
      required: true
    }
  },
  data() {
    return {
      store: Store.state,
      test: 1.1,
      universesList: []
    };
  },
  created() {
    axios.defaults.baseURL = process.env.VUE_APP_ROOT_API;
    const token = Store.getUserToken();
    if (token) {
      axios.defaults.headers.common["Cockpit-Token"] = token;
      axios
        .get("collections/get/universes")
        .then(response => {
          this.universesList = response.data.entries;
          this.store.productUniverses = this.universesList.map(
            u => u.universName
          );
          this.loader = false;
        })
        .catch(function(error) {
          console.log(error);
        });
    }

    // Initialisation des forms and sizes
    Store.initFormsAndSizes();
  },
  methods: {
    changeFormsAndSizes(event) {
      Store.changeFormsAndSizes(event.target.value);
    },
    checkboxChange(filter, type) {
      /**
       * Si un filtre a été mis sur l'autre unité, on le supprime
       * Sachant que lorsqu'on clic on a l'état précédent de la checkbox.
       * Donc si on fait passer la checkbox à True, ici on a filter.selectedUnit à false
       */

      Store.deleteFilter(
        filter.selectedUnit ? filter.technicalId[1] : filter.technicalId[0]
      );

      // Mise à jour des paramètres utilisés par le slider
      let unitMin = filter.selectedUnit
        ? filter.unit[0].valueMin
        : filter.unit[1].valueMin;
      let unitMax = filter.selectedUnit
        ? filter.unit[0].valueMax
        : filter.unit[1].valueMax;
      let unit = filter.selectedUnit
        ? filter.technicalId[0]
        : filter.technicalId[1];
      let operator = filter.selectedUnit
        ? filter.unit[0].operator
        : filter.unit[1].operator;

      filter.min = unitMin;

      if (!operator) {
        filter.max = unitMax;
        if (filter.selectedValue > unitMax) {
          filter.selectedValue = unitMax;
        }
        this.addFilter(unit, type, filter.selectedValue);
        return;
      }

      let newValue =
        operator == "multiply"
          ? (filter.selectedValue * filter.coefficient).toFixed(0)
          : (filter.selectedValue / filter.coefficient).toFixed(0);

      // Si la valeur courante est plus grande que la valeur max future
      if (newValue >= unitMax) {
        this.$nextTick(() => {
          filter.selectedValue = newValue;
          filter.max = unitMax;
          this.addFilter(unit, type, newValue);
        });
      } else {
        filter.max = unitMax;
        filter.selectedValue = newValue;
        this.addFilter(unit, type, filter.selectedValue);
      }
    },
    getIcon(iconName) {
      return require("@/assets/" + iconName);
    },
    addFilter(property, type, value) {
      Store.addFilter(property, type, value);
    },
    resetFilters() {
      this.clearSorting();
      Store.resetFilters(this.universesList);
    }
  }
};
</script>

