import Vue from "vue";
import Router from "vue-router";
import AlloysComparator from "./views/AlloysComparator.vue";
import LoginPage from "./views/LoginPage.vue";
import Store from "@/store";

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: "/",
      name: "alloysComparator",
      beforeEnter: guard,
      component: AlloysComparator
    },
    {
      path: "/login",
      name: "login",
      component: LoginPage
    }
  ]
});

function guard(to, from, next) {
  if (Store.userIsLoggedIn()) {
    next();
  } else {
    next("/login");
  }
}

export default router;
