<template>
  <div>
    <header>
      <img id="logo" src="@/assets/logoNavigator.png" alt="logo LBA" />
      <div class="row justify-content-end">
        <SearchBar />
      </div>
    </header>
    <div class="container-fluid">
      <!-- <SubHeader /> -->
      <div id="alloySelector" class="row">
        <div id="text-explanation" class="w-100 d-flex p-3">
          <p class="m-auto">
            <span>WELCOME!</span> This tool allows you to navigate through our
            key materials: by combining technical criteria on the left to
            pre-select them,
            <br />by short-listing your alloys of preference on
            the right, and by comparing them below via datasheets or diagrams.
          </p>
          <AlloysSorting ref="alloySorting" />
        </div>
        <div class="col-sm-5 col-md-4 no-padding">
          <AlloysFilters
            :alloys="store.alloys"
            :clear-selection="clearSelection"
            :clear-sorting="clearSorting"
          />
        </div>
        <div class="alloys-col">
          <div id="alloys-list" class="row m-0" @scroll="handleScroll">
            <AlloyItem
              v-for="alloy in store.alloys"
              :key="alloy.brandName + alloy.temper + alloy.isoDesignation"
              :alloy="alloy"
              :is-selected="isSelected(alloy)"
              :is-active="isActive(alloy)"
            />
            <p v-if="!store.alloys.length && !loader">
              Dear Sir, Dear Madam,
              <br />
              <br />Thank you for using our alloy navigator. It seems that your
              specifications doesn’t match any of the products we referenced,
              yet we have over 300 alloys in Lebronze Alloys.
              <br />
              <br />To know more about our wide offer of alloys, you can visit
              the following page and contact us through our website:
              <a
                target="_blank"
                href="https://www.lebronze-alloys.com/en/know-how-and-technologies/casting"
              >https://www.lebronze-alloys.com/en/know-how-and-technologies/casting</a>
            </p>
          </div>
        </div>
        <div class="arrows-ctn">
          <div id="arrows">
            <img
              id="right-arrow"
              src="@/assets/arrow.png"
              alt
              :class="{ active: activeArrowRight }"
              @click="scroll('right')"
            />
            <br />
            <img
              id="left-arrow"
              src="@/assets/arrow.png"
              alt
              :class="{ active: activeArrowLeft }"
              @click="scroll('left')"
            />
          </div>
        </div>
      </div>
      <DataVisualization :selected-alloys="selectedAlloys" @select-alloy="addSelectedAlloy" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import AlloyItem from "@/components/AlloyItem";
import AlloysFilters from "@/components/AlloysFilters";
import AlloysSorting from "@/components/AlloysSorting";
import DataVisualization from "@/components/DataVisualization";
import SearchBar from "@/components/SearchBar";
import Store from "@/store";
import $ from "jquery";

export default {
  name: "AlloysComparator",
  components: {
    AlloyItem,
    AlloysFilters,
    AlloysSorting,
    DataVisualization,
    SearchBar
  },
  data() {
    return {
      selectedAlloys: [],
      activeAlloy: {},
      store: Store.state,
      scrollLeft: 0,
      alloysListWidth: 0,
      loader: true
    };
  },
  computed: {
    activeArrowLeft() {
      return this.scrollLeft > 0 ? true : false;
    },
    activeArrowRight() {
      return this.alloysListWidth >= 0 ? true : false;
    }
  },
  created() {
    axios.defaults.baseURL = process.env.VUE_APP_ROOT_API;
    const token = Store.getUserToken();
    if (token) {
      axios.defaults.headers.common["Cockpit-Token"] = token;
      axios
        .get("collections/get/alliages")
        .then(response => {
          this.store.alloys = response.data.entries;
          this.store.alloysBackup = response.data.entries;
          Store.sortAlloysItems();
          this.loader = false;
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },
  methods: {
    handleScroll() {
      this.scrollLeft = $("#alloys-list").scrollLeft();
      this.alloysListWidth =
        $("#alloys-list").get(0).scrollWidth -
        $("#alloys-list").scrollLeft() -
        $("#alloys-list").width();
    },
    scroll(direction) {
      var leftPos = $("#alloys-list").scrollLeft();
      let value = 0;
      if (direction === "right") {
        value = leftPos + 200;
      } else {
        value = leftPos - 200;
      }
      $("#alloys-list").animate(
        {
          scrollLeft: value
        },
        "fast"
      );
    },
    isSelected(alloy) {
      return this.selectedAlloys.find(
        a => a.brandName === alloy.brandName && a.temper === alloy.temper
      )
        ? true
        : false;
    },
    isActive(alloy) {
      if (
        this.activeAlloy &&
        this.activeAlloy.brandName === alloy.brandName &&
        this.activeAlloy.temper === alloy.temper
      )
        return true;
      return false;
    },
    clickOnAlloy(alloy) {
      if (
        this.activeAlloy &&
        this.activeAlloy.brandName === alloy.brandName &&
        this.activeAlloy.temper === alloy.temper
      ) {
        this.addSelectedAlloy(alloy);
      } else {
        this.addActiveAlloy(alloy);
      }
    },
    deactivateAlloy() {
      this.activeAlloy = {};
    },
    addActiveAlloy(alloy) {
      this.activeAlloy = alloy;
    },
    clearSelection() {
      this.selectedAlloys = [];
    },
    clearSorting() {
      this.$refs.alloySorting.resetSorting();
    },
    addSelectedAlloy(alloy) {
      /* On regarde dans le tableau des alliages sélectionnés
        Pour voir si l'élément est présent
      */
      let alloyAlreadySelected = this.selectedAlloys.find(
        a => a.brandName === alloy.brandName && a.temper === alloy.temper
      );
      // Si c'est le cas, on le retire des éléments sélectionnés
      if (alloyAlreadySelected) {
        let index = this.selectedAlloys.indexOf(alloyAlreadySelected);
        this.selectedAlloys.splice(index, 1);
        this.activeAlloy = {};
      }
      // Sinon on l'ajoute
      else {
        if (this.selectedAlloys.length < 5) {
          this.selectedAlloys.push(alloy);
        }
      }
    }
  }
};
</script>

